.particles-js {
  position: relative;
  display: block;
  height: 100%;
  width: 100%;
  overflow: hidden; }

.particles-wrapper {
  height: 100%;
  width: 100%;
  overflow: hidden; }

.world-bg {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: .1;
  background: url("../assets/world.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center; }

.main-container {
  position: absolute;
  width: 100%;
  height: 100%; }

.logo-wrapper {
  position: relative;
  display: table;
  margin: 10vh auto 0;
  z-index: 999; }
  @media screen and (max-height: 650px) {
    .logo-wrapper img {
      width: 50vh; } }
  @media screen and (min-height: 650px) {
    .logo-wrapper {
      margin: 15vh auto 0; } }

.description-wrapper {
  position: relative;
  display: table;
  color: #fff;
  clear: left;
  margin: 0 auto;
  z-index: 999; }

.description-title {
  font-size: 1.6em;
  font-family: 'Raleway Thin', sans-serif;
  margin: 7vh 0 0;
  text-align: center; }
  .description-title.sub {
    font-size: 1.3em; }
  .description-title.second-sub {
    margin-top: 1.2em; }
  .description-title.third-sub {
    font-size: 1.2em;
    margin-top: 1em; }
  .description-title a {
    cursor: pointer; }
    .description-title a:link, .description-title a:active {
      color: inherit;
      text-decoration: none; }
  .description-title .bold {
    font-family: 'Raleway Bold', sans-serif; }
  @media screen and (min-width: 768px) and (min-height: 650px) {
    .description-title {
      font-size: 2em; }
      .description-title.sub {
        font-size: 1.8em; } }

.icon {
  padding: 0 .7em;
  width: 3em;
  height: 3em; }
  @media screen and (min-width: 768px) and (min-height: 650px) {
    .icon {
      width: 4em;
      height: 4em; } }

.desctiption-icons {
  text-align: center;
  margin-top: 2em; }



